import React ,{useMemo} from 'react';
import Header from './component/Header'

const Layout = (props) =>{

    const user = useMemo(()=>{
        return global.auth.getUser() || {}
    },[])

    return(
        <div className="main">
            <Header user={user}></Header>
            {props.children}
        </div>
    )
}


export default Layout;
